import React, { useEffect, useState } from 'react';
import ProductCard from '../components/ProductCard.jsx';
import axios from 'axios';


const ProductContainer = () => {
  const [products, setProducts] = useState([]);
  const [displayedProducts, setDisplayedProducts] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(0);

  useEffect(() => {
    axios.get('https://api.bestbuy.com/v1/products(customerReviewAverage>=4.8&customerReviewCount>=15)?sort=image.asc&show=image,name,salePrice,customerReviewAverage,customerReviewCount,upc&pageSize=100&format=json&apiKey=irf4cGUREwStIE6b97zuktVm')
  .then(res => {
    console.log(res.data);
    // products = res.data.products;
    setProducts(res.data.products);
    setDisplayedProducts(res.data.products.slice(0, 20));
  })
  .catch((err) => {
    console.log(err);
  });
  }, []);

  const handleNextClick = () => {
    const nextIndex = currentIndex + 20;
    if (nextIndex < products.length) {
      setDisplayedProducts(products.slice(nextIndex, nextIndex + 20));
      setCurrentIndex(nextIndex);
    }
  };

  return (
    <div>
      {displayedProducts.map((product) => (
        <ProductCard key={product.upc} product={product} />
      ))}
      <button onClick={handleNextClick}>Next</button>
    </div>
  );
};


export default ProductContainer;